body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.nav-text {
    color: #ebf0f8;  
}
.backcolor{
    background-color: #132238 !important;
}
ul.nav.nav-tabs.h4.p-3.fixed-top{
    
    padding-bottom: 0px !important;
  
}
.nav-link:hover .nav-text { 
    color:#98ccd3;
}
i.nav-text:hover{
    color:#98ccd3
}

.card{
    background-color:#EBF0F6;
}
ul{
    margin: 0; 
}
.libraries-icons{
    margin:3%
}
li.list-inline-item:hover{
    -webkit-transform: scale(1.5);
}
.pic:hover{
opacity: .7; 
background-color:#98ccd3 !important;
font-weight: 100px;
color:#132238;
}
div a{
    color:#132238;
    font-weight: bold;
}
.pic:hover img {
    /* -webkit-transform:translate(0px,-8rem); */
    -webkit-transform-origin: bottom left;
    -webkit-transform: rotate(-45deg) translate(0,-4rem);
    -webkit-transition: .5s;
    transition: .5s
}
.card-text{
     color:transparent;
     -webkit-transform: translate(0,-5rem);
     
     
}

.pic:hover .card-text{
    color:#132238;
    -webkit-transition: 2s;
    transition: 2s;
}
button{
    background-color:transparent;
    border: solid !important;
    border-color:#364e68 !important; 
}
button:hover{
    background-color:#132238;
    color:#Ebf0f6 !important;
    -webkit-text-decoration: underline solid #ebf0f6 !important;
            text-decoration: underline solid #ebf0f6 !important;
}
.activeBtn{
    background-color:#132238;
    color:#Ebf0f6 !important;
}






